import React from 'react'

const About = () => {
  return (
    <>
      <section className='sec_1'>
        <div className='header_height sec_bg d-flex align-items-center'>
          <div className='container'>
            <div className='col-lg-11 mx-auto py-5'>
              <p className='home-header container d-flex flex-column align-items-left mb-2'>
                About
              </p>
              <span className='white-line-top'></span>
              <p className='abouttext'>
                Sky Production is a premiere recording facility located in
                downtown Hong kong. Specializing in audio production, mixing,
                mastering and project development, Sky Productions is here to
                help bring artists' vision to life. We bridge the gap between
                intangible and tangible, while also helping to connect artists
                with resources and opportunities to perpetuate their careers.
                It's our goal to be the guiding light for independent creatives
                in music and various other mediums. At Sky Productions, we
                remain dedicated to connecting creatives and cultivating
                culture.
              </p>
              <p className='grow-with-us'>
                <strong>
                  <em>
                    <a href='http://localhost:3001/contact'>GROW WITH US</a>
                  </em>
                </strong>
              </p>
              <span className='white-line-bottom'></span>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default About
