import React from 'react'
import { Carousel } from 'react-responsive-carousel'
import 'react-responsive-carousel/lib/styles/carousel.min.css' // Import carousel styles
import FormatQuoteIcon from '@mui/icons-material/FormatQuote'
import Oceanpark from '../logos/oceanpark.png'
import Oxford from '../logos/OUP_logo.svg'
import Pearson from '../logos/Pearson_plc_logo.svg'
import BOC from '../logos/Bank_of_China.svg'
import Hospital from '../logos/Hospital_Authority_logo.svg'
import HSBC from '../logos/HSBC.svg'

const Clients = () => {
  return (
    <>
      <section className='sec_1'>
        <div className='header_height sec_bg d-flex align-items-center'>
          <div className='container'>
            <div className='col-lg-11 mx-auto py-5'>
              <div className='samples'>
                <p className='home-header container d-flex flex-column align-items-left mb-2'>
                  Samples
                </p>
                <span className='white-line-top'></span>
                <div>
                  <div>
                    <h6>The Recording and Mixing sample</h6>
                    <span className='white-line-samples'></span>
                  </div>
                  <div>
                    <p>
                      <strong>Artist:</strong> Talie, <strong>Producer:</strong>{' '}
                      Dan
                    </p>
                  </div>
                  <div>
                    <p>
                      <strong>Artist:</strong> Zoey
                    </p>
                  </div>
                  <div>
                    <p>
                      <strong>Artist:</strong> Jonathan
                    </p>
                  </div>
                  <div>
                    <p>
                      <strong>Artist:</strong> Aty
                    </p>
                  </div>
                </div>
                <div style={{ paddingTop: '20px' }}>
                  <div>
                    <h6>The Audio Sample of Sound Design</h6>
                    <span className='white-line-samples'></span>
                  </div>
                  <div>
                    <p>
                      <strong>Game VO:</strong> Transformer
                    </p>
                  </div>
                </div>
                <div style={{ paddingTop: '20px' }}>
                  <div>
                    <h6>
                      The Sample Of Music Composition (Animation, Game and
                      Corporate Video)
                    </h6>
                    <span className='white-line-samples'></span>
                  </div>
                  <div>
                    <p>
                      <strong>Recording Guitar Artist:</strong> Yat Fung
                    </p>
                  </div>
                  <div>
                    <p>
                      <strong>Recording Viola</strong>
                    </p>
                  </div>
                  <div>
                    <p>
                      <strong>Recording Trio</strong>
                    </p>
                  </div>
                </div>
              </div>
              <div className='clients mb-2'>
                {' '}
                <p className='home-header container d-flex flex-column align-items-left mb-2'>
                  Clients
                </p>
                <span className='white-line-top'></span>
                <div>
                  <Carousel
                    autoPlay
                    infiniteLoop
                    interval={3500}
                    transitionTime={1000}
                    showThumbs={false}
                    showArrows={false}
                    showStatus={false}
                    showIndicators={false}
                  >
                    <div
                      style={{
                        display: 'flex',
                        justifyContent: 'space-around'
                      }}
                    >
                      <img src={Oceanpark} alt='' style={{ width: '260px' }} />
                      <img src={Oxford} alt='' style={{ width: '250px' }} />
                      <img src={HSBC} alt='' style={{ width: '250px' }} />
                    </div>
                    <div
                      style={{
                        display: 'flex',
                        justifyContent: 'space-around'
                      }}
                    >
                      <img src={BOC} alt='' style={{ width: '260px' }} />
                      <img src={Hospital} alt='' style={{ width: '250px' }} />
                      <img
                        src={Pearson}
                        alt=''
                        style={{ width: '250px', paddingTop: '20px' }}
                      />
                    </div>
                  </Carousel>
                </div>
              </div>
              <div className='feedback pt-5'>
                <div className='mx auto' style={{ textAlign: 'center' }}>
                  <h3>What Our Client Say</h3>
                  <span className='white-line-feedback'></span>
                </div>
                <div style={{ textAlign: 'center' }}>
                  <FormatQuoteIcon
                    style={{
                      color: '#6badf9',
                      transform: 'rotate(180deg)',
                      fontSize: '60px',
                      marginBottom: '20px'
                    }}
                  />
                </div>
                <div className='feedback-comment'>
                  <p>
                    When I came to Hong Kong 9 years ago from the BBC in London,
                    I spent many frustrating hours recording in sub standard
                    Production houses, incredulous that such audio ...
                  </p>
                </div>
                <div className='feedback-comment-name'>
                  <p>David Pope</p>
                  <p>All Voice Talent</p>
                </div>
              </div>
              <span className='white-line-bottom'></span>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default Clients
