import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import { CloudOutlined } from '@ant-design/icons' // Import the icon you're using

const Navbar = () => {
  const [navOpen, setNavOpen] = useState(false)
  const handleToggle = () => {
    setNavOpen(!navOpen)
  }

  return (
    <>
      <div className='container-fluid navigation_bar'>
        <nav className='navscroll navbar fixed-top navbar-expand-lg navbar-light py-2 px-3'>
          <div className='container d-flex flex-column align-items-left mb-2'>
            <div className='logo mb-4 pt-1'>
              <a href='sky-production/' title='Sky Productions' rel='home'>
                Sky Productions <CloudOutlined className='cloud-icon' />
              </a>
            </div>
            <div className='d-flex justify-content-end'>
              <button
                className='navbar-toggler'
                type='button'
                onClick={handleToggle}
              >
                <span className='navbar-toggler-icon'></span>
              </button>
            </div>
            <div
              className={`collapse navbar-collapse ${navOpen ? 'show' : ''}`}
              id='navbarSupportedContent'
            >
              <ul className='navbar-nav ml-auto'>
                <li className='nav-item custom-nav-item'>
                  <NavLink exact className='nav-link' to='sky-production/'>
                    HOME
                  </NavLink>
                </li>
                <li className='nav-item custom-nav-item'>
                  <NavLink exact className='nav-link' to='sky-production/about'>
                    ABOUT
                  </NavLink>
                </li>
                <li className='nav-item custom-nav-item'>
                  <NavLink
                    exact
                    className='nav-link'
                    to='sky-production/equipment'
                  >
                    EQUIPMENT
                  </NavLink>
                </li>
                <li className='nav-item custom-nav-item'>
                  <NavLink
                    exact
                    className='nav-link'
                    to='sky-production/clients'
                  >
                    CLIENTS AND SAMPLES
                  </NavLink>
                </li>
                <li className='nav-item custom-nav-item'>
                  <NavLink
                    exact
                    className='nav-link'
                    to='sky-production/the-creatives'
                  >
                    THE CREATIVES
                  </NavLink>
                </li>
                <li className='nav-item custom-nav-item'>
                  <NavLink
                    exact
                    className='nav-link'
                    to='sky-production/contact'
                  >
                    CONTACT
                  </NavLink>
                </li>
              </ul>
              <span className='header_shadow' id='header_shadow'></span>
            </div>
          </div>
        </nav>
      </div>
    </>
  )
}

export default Navbar
