import React from 'react'
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import image2 from '../image1.jpg'
import image1 from '../image2.jpg'
import image3 from '../image3.jpg'
import { Carousel } from 'react-responsive-carousel'
import 'react-responsive-carousel/lib/styles/carousel.min.css' // Import carousel styles
import CallIcon from '@mui/icons-material/Call'
import EmailIcon from '@mui/icons-material/Email'

const Home = () => {
  return (
    <>
      <section className='sec_1'>
        <div className='header_height sec_bg d-flex align-items-center'>
          <div className='container pt-3'>
            <div className='col-12 col-md-8 col-lg-12 mx-auto py-5'>
              <p className='home-header1 container d-flex flex-column align-items-left'>
                Recording | Music Production | Video Production
              </p>
              <span className='white-line-top'></span>
              <div style={{ paddingBottom: '10px' }}>
                <p className='homepage-title'>
                  "Sky Productions provides a state-of-the-art recording studio,
                  cutting-edge audio editing software and mastering facilities
                  for all formats"
                  <br /> - Chris Fong (Studio Manager)
                </p>
              </div>
              <div className='gallery'>
                <Carousel
                  autoPlay
                  infiniteLoop
                  interval={3500}
                  transitionTime={1000}
                  showThumbs={false}
                  showArrows={false}
                  showStatus={false}
                  showIndicators={false}
                >
                  <div className='slider-image'>
                    <img src={image1} alt='Image 1' />
                    <div className='carouselcaption1'>
                      <h5>Recording</h5>
                    </div>
                    <h6 style={{ lineHeight: '1.4em' }}>
                      Mixing and Mastering
                    </h6>
                  </div>
                  <div className='slider-image'>
                    <img src={image2} alt='Image 2' />
                    <div className='carouselcaption1'>
                      <h5>Voice Over Service</h5>
                    </div>
                    <h6 style={{ lineHeight: '1.4em' }}>
                      English, Cantonese and Mandarin
                    </h6>
                  </div>
                  <div className='slider-image'>
                    <img src={image3} alt='Image 3' />
                    <div className='carouselcaption1'>
                      <h5>Video Productions</h5>
                    </div>
                    <h6 style={{ lineHeight: '1.4em' }}>
                      Video shooting and Editing
                    </h6>
                  </div>
                </Carousel>
              </div>
              <span className='white-line-bottom'></span>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default Home
