import React from 'react'
import Outboard from '../outboard.webp'
import U87 from '../u87.jpg'
import Converter from '../converter.jpg'
import Speaker from '../speaker.jpg'
import Mac from '../Mac.jpg'
import Daw from '../daw.jpeg'

const Equipment = () => {
  return (
    <>
      <section className='sec_1'>
        <div className='header_height sec_bg d-flex align-items-center'>
          <div className='container'>
            <div className='col-lg-11 mx-auto py-5'>
              <p className='home-header container d-flex flex-column align-items-left mb-2'>
                Equipment
              </p>
              <span className='white-line-top'></span>
              <div className='Studio-content'>
                <div className='row-content reverse mb-5'>
                  <div className='content'>
                    <p className='Outboard' style={{ color: '#4e4a4ad8' }}>
                      <em>
                        <strong style={{ color: '#6badf9' }}>Outboard</strong>
                      </em>
                      <span className='white-line-head'></span>
                      LA 610
                      <br />
                      Avalon 737 (2)
                      <br />
                      Original Vintage Neve 1073's (2)
                      <br />
                      Tube Tech CL-1B
                      <br />
                      GML 8200
                      <br />
                      UA 2-1176
                      <br />
                    </p>
                  </div>
                  <div className='image'>
                    <img
                      src={Outboard}
                      alt='Outboard'
                      className='image-hover'
                      style={{ width: '255px', boxShadow: '1px 0px 6px 1px' }}
                    />
                  </div>
                </div>
                <div className='row-content mb-5'>
                  <div className='content'>
                    <p className='Converters' style={{ color: '#4e4a4ad8' }}>
                      <em>
                        <strong style={{ color: '#6badf9' }}>
                          Converters and Interfaces
                        </strong>
                      </em>
                      <span className='white-line-head'></span>
                      God Mod Digi 002
                      <br />
                      BLA Staff Series Sparrow A/D Converter (White model)
                      <br />
                      Mytek Stereo 192 DSD-DAC
                      <br />
                      Mbox 2 Pro
                      <br />
                    </p>
                  </div>
                  <div className='image'>
                    <img
                      src={Converter}
                      alt='Converters'
                      className='image-hover'
                      style={{
                        width: '255px',
                        height: '160.12px',
                        boxShadow: '1px 0px 6px 1px'
                      }}
                    />
                  </div>
                </div>
                <div className='row-content reverse mb-5'>
                  <div className='content'>
                    <p className='Microphones' style={{ color: '#4e4a4ad8' }}>
                      <em>
                        <strong style={{ color: '#6badf9' }}>
                          Microphones
                        </strong>
                      </em>
                      <span className='white-line-head'></span>
                      <strong>Neumann U87 </strong>(2)
                      <br />
                      Neumann TLM49
                      <br />
                      Neumann TLM102 (2)
                      <br />
                      Shure KSM 32 (2)
                      <br />
                    </p>
                  </div>
                  <div className='image'>
                    <img
                      src={U87}
                      alt='Outboard'
                      className='image-hover'
                      style={{ width: '180px', boxShadow: '1px 0px 6px 1px' }}
                    />
                  </div>
                </div>
                <div className='row-content mb-5'>
                  <div className='content'>
                    <p className='Speakers' style={{ color: '#4e4a4ad8' }}>
                      <em>
                        <strong style={{ color: '#6badf9' }}>Speakers</strong>
                      </em>
                      <span className='white-line-head'></span>
                      Mackie HR8 MK2's
                      <br />
                      Yamaha NS-10's
                      <br />
                      Yamaha HS 50's
                      <br />
                      KRK RP8G2's
                      <br />
                    </p>
                  </div>
                  <div className='image'>
                    <img
                      src={Speaker}
                      alt='Converters'
                      className='image-hover'
                      style={{ width: '255px', boxShadow: '1px 0px 6px 1px' }}
                    />
                  </div>
                </div>
                <div className='row-content reverse mb-5'>
                  <div className='content'>
                    <p className='Computers' style={{ color: '#4e4a4ad8' }}>
                      <em>
                        <strong style={{ color: '#6badf9' }}>Computers</strong>
                      </em>
                      <span className='white-line-head'></span>
                      (2) Mac Pro - 2 x 2.8Ghz Quad-Core Intel Xeon Processor /
                      10 GB Ram / Pro Tools 10 / Pro Tools 10 HD
                      <br />
                      Custom Production PC - 3.0Ghz Quad Core 17 Processor / 16
                      GB Ram / Pro Tools 10
                      <br />
                    </p>
                  </div>
                  <div className='image'>
                    <img
                      src={Mac}
                      alt='Outboard'
                      className='image-hover'
                      style={{ width: '255px', boxShadow: '1px 0px 6px 1px' }}
                    />
                  </div>
                </div>
                <div className='row-content mb-5'>
                  <div className='content'>
                    <p className='Programs' style={{ color: '#4e4a4ad8' }}>
                      <em>
                        <strong style={{ color: '#6badf9' }}>
                          Programs / DAWs Used
                        </strong>
                      </em>
                      <span className='white-line-head'></span>
                      Pro Tools 10 / Pro Tools 10 HD
                      <br />
                      Logic Pro X
                      <br />
                      Ableton Live 9
                      <br />
                      FL Studio 11
                      <br />
                    </p>
                  </div>
                  <div className='image'>
                    <img
                      src={Daw}
                      alt='Converters'
                      className='image-hover'
                      style={{
                        width: '255px',
                        opacity: '0.75',
                        boxShadow: '1px 0px 6px 1px'
                      }}
                    />
                  </div>
                </div>
              </div>
              <span className='white-line-bottom'></span>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default Equipment
