import React from 'react'
import FacebookIcon from '@mui/icons-material/Facebook'
import InstagramIcon from '@mui/icons-material/Instagram'
import GoogleIcon from '@mui/icons-material/Google'
import WhatsAppIcon from '@mui/icons-material/WhatsApp'

const Footer = () => {
  return (
    <>
      <footer className='footerstyle'>
        <div className='container d-lg-flex align-items-center justify-content-between py-3 small text-center'>
          <div className='text-white large'>
            Copyright © 2023 Sky Productions
          </div>
          <div>
            <a
              className='mx-2 icon-link1'
              style={{ cursor: 'pointer' }}
              href='https://www.facebook.com/chrisfongff/'
              target='_blank'
            >
              <FacebookIcon fontSize='medium' />
            </a>
            <a
              className='mx-2 icon-link2'
              style={{ cursor: 'pointer' }}
              href='https://www.instagram.com/skyproductionshk/'
              target='_blank'
            >
              <InstagramIcon fontSize='medium' />
            </a>
            <a
              className='mx-2 icon-link3'
              style={{ cursor: 'pointer' }}
              href='https://plus.google.com/SkyproductionsHk'
              target='_blank'
            >
              <GoogleIcon fontSize='medium' />
            </a>
            <a
              href='https://wa.me/85292229145'
              className='mx-2 icon-link4'
              style={{ cursor: 'pointer' }}
              target='_blank'
              rel='noopener noreferrer'
            >
              <WhatsAppIcon fontSize='medium' />
            </a>
          </div>
        </div>
      </footer>
    </>
  )
}

export default Footer
